/* individual posts */
.posts-index-one {
  max-width: 800px;
  color: black;
  padding: .5rem;
  background: #f5efe7;
  -webkit-box-shadow: 0 1px 4px $primary rgba(0, 0, 0, 0.4);
          box-shadow: 0 1px 4px $primary rgba(0, 0, 0, 0.4);
  margin: 10px 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;

  word-break: break-word;
 overflow: hidden;
 -o-text-overflow: ellipsis;
    text-overflow: ellipsis;

 /* line-height: 16px; */
 /* max-height: 310px; */
}

/* all posts on explore page */
.posts-index-all {
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: -7vw;
  margin-right: -7vw;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}



